.p-content6 {
  grid-row: 8 /9;
  grid-column: 1 /-1;
  padding-top: 5rem;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__container {
    display: flex;
    margin-bottom: 6.5rem;

    @media screen and (max-width: 767px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }
  &__box {
    display: flex;
    flex-direction: column;
    align-items: center;
    &:not(:last-child) {
      margin-right: 12.2rem;
      @media screen and (max-width: 767px) {
        margin-bottom: 4rem;
        margin-right: 0;
      }
    }
  }
  &__person {
    display: flex;
    align-items: center;
  }
  &__name {
    margin-left: 2rem;
    p {
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      line-height: 1;
      margin-bottom: 8px;
      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 1;
        margin-bottom: 1rem;
      }
    }
    span {
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      line-height: 1;
      color: #888888;
      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 1;
      }
    }
  }

  &__detail {
    position: relative;
    width: 35.8rem;
    padding-top: 3.1rem;
    padding-left: 2.8rem;
    padding-right: 2.8rem;
    padding-bottom: 3.2rem;
    border: 1px solid #eeeeee;
    background-color: #ffff;
    z-index: 0;
    border-radius: 1rem;
    margin-top: 3.8rem;
    @media screen and (max-width: 767px) {
      width: 80%;
      border-radius: 2rem;
    }
    &::before {
      position: absolute;
      z-index: 3;
      top: -1.4rem;
      left: 50%;
      transform: translate(-50%, 0);
      display: inline-block;
      content: "";
      height: 8.4rem;
      width: 15rem;
      border: 2px solid #efefef;
      background-color: #ffff;
      border-radius: 1rem;
      clip-path: polygon(50% 0, 50% 0, 100% 100%, 0 100%);
    }
    p {
      position: relative;
      z-index: 4;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      line-height: 2.6rem;
      color: #444444;
      background-color: #ffff;

      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 2;
      }

      i {
        display: inline-block;
        font-size: 2.2rem;
        height: 20px;
        margin-right: 2.9rem;

        @media screen and (max-width: 767px) {
          font-size: 3.2rem;
        }
      }
    }
  }

  &__brand {
    margin-bottom: 5.5rem;
    display: flex;
    width: 138rem;
    align-items: center;
    justify-content: space-around;
    @media screen and (max-width: 767px) {
      width: 100%;
    }
  }
}
