//------------------------------------------------------------
// margin
//------------------------------------------------------------
.is-alignCenter {
  align-self: center;
}

.is-alignEnd {
  align-self: flex-end;
}

.is-bar-footer {
  line-height: 3.3rem;
  padding-top: 0;
  border-top: 1px solid$color-white-dark;
  border-bottom: 1px solid $color-white-dark;
  max-width: 100%;
}

//------------------------------------------------------------
// color
//------------------------------------------------------------

.is-title--yellow {
  border-top: 1px solid $color-yellow;
  border-bottom: 1px solid $color-yellow;
  border-right: 1px solid $color-yellow;
  border-left: 7px solid $color-yellow;
  background-color: rgba($color: $color-yellow, $alpha: 0.2);
}

.is-title--orange {
  border-top: 1px solid $color-orange;
  border-bottom: 1px solid $color-orange;
  border-right: 1px solid $color-orange;
  border-left: 7px solid $color-orange;
  background-color: rgba($color: $color-orange, $alpha: 0.2);
}

.is-title--blue {
  border-top: 1px solid $color-blue;
  border-bottom: 1px solid $color-blue;
  border-right: 1px solid $color-blue;
  border-left: 7px solid $color-blue;
  background-color: rgba($color: $color-blue, $alpha: 0.2);
}

.is--orange {
  background-color: $color-orange;
}

.is--green {
  background-color: $color-green;
}
.is--green-dark {
  background-color: $color-green-dark;
}

.is--yellow {
  background-color: $color-yellow-light;
}

.is--pink {
  background-color: $color-pink-light;
}

.is--pink-lighter {
  background-color: $color-pink-lighter;
}

.is--blue {
  background-color: $color-blue;
}

.is-color-orange {
  color: $color-orange-light !important;
}

.is-color-gray-lighter {
  background-color: $color-gray-lighter;
}

//JAVASCRIPT EFFECT

.is--active {
  font-weight: bold;
}

.is--width11 {
  width: 11.2rem;
}
.is--width9 {
  width: 9.3rem;
}
.is-width10 {
  width: 10.1rem;
}

.is-width8 {
  width: 8.5rem;
}

.is--textCenter {
  text-align: center;
}
