/* Header
------------------------------------------------------------*/
.l-header {
  display: flex;
  justify-content: space-between;
  height: 7.9rem;
  padding: 1.2rem 13.7rem 1.2rem 11.4rem;

  &__menu {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    margin-top: 1.2rem;
    li {
      list-style: none;
      transition: all 0.2s;
      a {
        display: inline-block;
        text-decoration: none;
        font-family: "Yahei-regular";
        font-size: 1.4rem;
        line-height: 1;
        color: #444444;
      }
    }

    li:nth-of-type(1) {
      margin-right: 6.7rem;
      margin-top: 3px;
      padding-bottom: 0.8rem;
      border-bottom: 2px solid transparent;
      &:hover {
        padding-bottom: 0.8rem;
        border-bottom: 2px solid #1c9eb4;
      }
    }
    li:nth-of-type(2) {
      margin-right: 6.6rem;
      margin-top: 3px;
      padding-bottom: 0.8rem;
      border-bottom: 2px solid transparent;
      a {
        position: relative;
        z-index: 1;
      }
      img {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(50%, -30%);
      }

      &:hover {
        padding-bottom: 0.8rem;
        border-bottom: 2px solid #1c9eb4;
      }
    }

    li:nth-of-type(3) {
      margin-right: 6rem;
      margin-top: 3px;
      padding-bottom: 0.8rem;
      border-bottom: 2px solid transparent;

      &:hover {
        padding-bottom: 0.8rem;
        border-bottom: 2px solid #1c9eb4;
      }
    }
    li:nth-of-type(4) {
      margin-right: 6.1rem;
      margin-top: 3px;
      padding-bottom: 0.8rem;
      border-bottom: 2px solid transparent;

      &:hover {
        padding-bottom: 0.8rem;
        border-bottom: 2px solid #1c9eb4;
      }
    }

    li:nth-of-type(5) {
      margin-right: 6.8rem;
      margin-top: 3px;
      padding-bottom: 0.8rem;
      border-bottom: 2px solid transparent;
      &:hover {
        padding-bottom: 0.8rem;
        border-bottom: 2px solid #1c9eb4;
      }
    }

    li:nth-of-type(6) {
      margin-right: 5.9rem;
    }
    li:nth-of-type(7) {
      margin-right: 5.9rem;
    }

    li:nth-of-type(8) {
      margin-right: 4rem;
    }

    li:nth-of-type(9) {
      padding-right: 1.8rem;
      border-right: 1px solid #adadad;
      a {
        color: #1c9eb4;
      }
    }

    li:nth-of-type(10) {
      padding-left: 1.8rem;
      border-left: 1px solid #7a7a7a;
    }
  }
}
/* Main menu
------------------------------------------------------------*/
