/* layout
------------------------------------------------------------*
*/
body {
  display: grid;
  grid-template-columns: 10rem 17rem auto 17rem 10rem;
  grid-template-rows: repeat(10, auto);

  @media screen and (max-width: 767px) {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(10, auto);
  }
}

.l-header {
  grid-row: 1 / 2;
  grid-column: 1 /-1;
  @media screen and (max-width: 767px) {
    display: none;
  }
}
