.p-content1 {
  grid-row: 3/4;
  grid-column: 1/ -1;
  text-align: center;
  padding-top: 6.5rem;
  @media screen and (max-width: 767px) {
    padding-top: 8rem;
  }
  h1 {
    font-family: "Yahei-regular";
    font-size: 2.4rem;
    line-height: 3.9rem;
    color: #333333;
    margin-top: 1.5rem;
    margin-bottom: 4rem;

    @media screen and (max-width: 767px) {
      font-size: 4.4rem;
      line-height: 2;
      width: 60rem;
      margin: 0 auto;
      margin-bottom: 5rem;
    }
  }

  p {
    font-family: "Yahei-regular";
    font-size: 1.4rem;
    line-height: 2.9rem;
    color: #222222;
    margin-left: 9px;
    margin-bottom: 4.1rem;
    @media screen and (max-width: 767px) {
      font-size: 3rem;
      line-height: 2;
      margin-left: 0;
      padding-left: 6rem;
      padding-right: 6rem;
    }
  }

  button {
    font-family: "Yahei-regular";
    font-size: 1.4rem;
    color: #111111;
    line-height: 5.7rem;
    width: 17.7rem;
    border: 2px solid #999999;
    background-color: #ffffff;
    transition: all 0.2s;
    @media screen and (max-width: 767px) {
      font-size: 2.4rem;
      width: 25.7rem;
      line-height: 7rem;
      margin-bottom: 9.5rem;
    }
    img {
      display: inline-block;
      margin-left: 1rem;
      @media screen and (max-width: 767px) {
        margin-left: 4rem;
      }
    }

    &:not(:last-child) {
      margin-right: 8.6rem;
    }

    &:hover {
      background-color: #1c9eb4;
      border: 2px solid #1c9eb4;
      color: white;
    }

    margin-bottom: 8.5rem;
  }

  hr {
    border-top: 0;
    border-bottom: 1px solid #999999;
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  &__steps {
    display: flex;
    justify-content: center;
    column-gap: 10.2rem;
    @media screen and (max-width: 767px) {
      display: flex;
      flex-direction: column;
    }

    span {
      display: inline-block;
      font-family: "Yahei-regular";
      font-size: 1.8rem;
      line-height: 3rem;
      color: #333333;
      margin-bottom: 2.5rem;
      @media screen and (max-width: 767px) {
        font-size: 3.8rem;
        line-height: 1;
        margin-bottom: 3.5rem;
      }
    }
    p {
      font-family: "Yahei-regular";
      font-size: 1.6rem;
      line-height: 2.8rem;
      color: #333333;

      @media screen and (max-width: 767px) {
        font-size: 2.6rem;
        line-height: 1;
      }
    }
  }

  &__step {
    transform: translateY(-5px);
    @media screen and (max-width: 767px) {
      margin-bottom: 2rem;
    }
    &::before {
      display: block;
      content: "";
      width: 1rem;
      height: 1rem;
      background-color: #666666;
      margin: 0 auto;
      margin-bottom: 3.1rem;
      @media screen and (max-width: 767px) {
        width: 100%;
        height: 4rem;
      }
    }
    // &:not(:last-child) {
    //   margin-right: 11.8rem;
    // }
    &:hover {
      &::before {
        background-color: #1c9eb4;
      }
      span,
      p {
        color: #1c9eb4;
      }
    }
  }
}
