.p-banner {
  grid-row: 2/ 3;
  grid-column: 1 /-1;
  background-image: url(../img/body/banner/background1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-top: 32.5rem;
  padding-bottom: 33rem;
  @media screen and (max-width: 767px) {
    width: 100%;
    padding-top: 35.5rem;
    padding-bottom: 35.5rem;
  }
  p {
    font-family: "Yahei-regular";
    font-size: 3rem;
    color: #ffffff;
    text-align: center;
    line-height: 4.6rem;
    margin-left: 2rem;

    @media screen and (max-width: 767px) {
      font-size: 5rem;
      line-height: 2;
      width: 78rem;
      margin: 0 auto;
    }
  }
}
