.p-content5 {
  grid-row: 7 /8;
  grid-column: 1 /-1;
  background-image: linear-gradient(to right, rgba(#0000, 0.3)),
    url(../img/body/content5/background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  padding-top: 25.4rem;
  padding-bottom: 27.3rem;
  h1 {
    font-family: "Yahei-regular";
    font-weight: bold;
    font-size: 3.6rem;
    line-height: 6.6rem;
    color: #ffffff;
    margin-bottom: 0.2rem;
    letter-spacing: 0.05em;
    @media screen and (max-width: 767px) {
      font-size: 6.8rem;
      line-height: 1;
      letter-spacing: 0.1em;
      margin-bottom: 4rem;
    }
  }

  p {
    font-family: "Yahei-regular";
    color: #ffffff;
    font-size: 2.4rem;
    line-height: 5.4rem;
    letter-spacing: 0.05em;
    margin-bottom: 2.8rem;
    @media screen and (max-width: 767px) {
      font-size: 4.4rem;
      line-height: 1;
      letter-spacing: 0.1em;
      margin-bottom: 8rem;
    }
  }

  span {
    font-family: "Yahei-regular";
    color: #ffffff;
    font-size: 1.6rem;
    letter-spacing: 0.05em;
    @media screen and (max-width: 767px) {
      font-size: 3.6rem;
      line-height: 1;
      letter-spacing: 0.1em;
    }
    i {
      margin-left: 1rem;
    }
    &:not(:last-child) {
      margin-right: 8rem;
    }
  }
}
