.p-content2 {
  grid-row: 4/ 5;
  grid-column: 1/ -1;
  display: flex;
  @media screen and (max-width: 767px) {
    flex-direction: column;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  &__left,
  &__right {
    max-width: 50%;

    @media screen and (max-width: 767px) {
      min-width: 100%;
    }
  }

  &__left {
    position: relative;
    &--overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      // background-color: rgba(#005c57, 0.3);
      background-color: rgba(#0000, 0.25);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      p {
        font-family: "Yahei-regular";
        font-size: 3.6rem;
        line-height: 6rem;
        color: #ffffff;
        width: 29rem;
        margin-left: -3rem;

        @media screen and (max-width: 767px) {
          font-size: 6.6rem;
          line-height: 1.4;
          width: 53rem;
        }
      }
      span {
        font-family: "Yahei-regular";
        font-size: 1.8rem;
        line-height: 4.8rem;
        color: #ffffff;
        position: absolute;
        bottom: 2rem;
        right: 5rem;
        text-transform: uppercase;
        @media screen and (max-width: 767px) {
          font-size: 3.8rem;
          line-height: 1;
          bottom: 5rem;
          right: 7rem;
        }
      }
    }
  }

  &__right {
    display: grid;
    grid-template-rows: repeat(4, auto);
    grid-template-columns: repeat(4, 25%);
    figure {
      position: relative;
      transition: all 0.2s;
      &:hover {
        .p-content2__right--overlay {
          display: flex;
        }
      }
    }
    figure:nth-of-type(1) {
      grid-row: 1 / 2;
      grid-column: 1 / 2;
    }

    figure:nth-of-type(2) {
      grid-row: 1 / 2;
      grid-column: 2 / 3;
    }
    figure:nth-of-type(3) {
      grid-row: 2 / 3;
      grid-column: 2 / 3;
    }
    figure:nth-of-type(4) {
      grid-row: 2 / 3;
      grid-column: 1 / 2;
    }
    figure:nth-of-type(5) {
      grid-row: 3 / 4;
      grid-column: 3 / 4;
    }
    figure:nth-of-type(6) {
      grid-row: 3 / 4;
      grid-column: 4 / 5;
    }
    figure:nth-of-type(7) {
      grid-row: 4 / 5;
      grid-column: 4 / 5;
    }
    figure:nth-of-type(8) {
      grid-row: 3 / 5;
      grid-column: 1 / 3;
    }
    figure:nth-of-type(9) {
      grid-row: 4 / 5;
      grid-column: 3 / 4;
    }
    figure:nth-of-type(10) {
      grid-row: 1 / 3;
      grid-column: 3 / 5;
    }

    &--overlay {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      // background-color: rgba(#4e6045, 0.55);
      background-color: rgba(#0000, 0.55);
      display: flex;
      justify-content: center;
      align-items: center;
      display: none;
      span {
        font-family: "Yahei-regular";
        font-size: 1.6rem;
        line-height: 4.6rem;
        color: #ffffff;
        @media screen and (max-width: 767px) {
          font-size: 3.8rem;
          line-height: 1;
        }

        &:not(:last-child) {
          margin-right: 6.3rem;
        }
      }
    }
  }
}
