.c-sidebar {
  position: fixed;
  right: 0;
  top: 31rem;
  display: flex;
  flex-direction: column;
  figure {
    width: 6.5rem;
    background-color: rgba(#2a2a2a, 0.8);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 767px) {
      width: 12rem;
    }
    img {
      margin-bottom: 1.1rem;
      @media screen and (max-width: 767px) {
        margin-bottom: 2.1rem;
      }
    }
    span {
      font-family: "Yahei-regular";
      font-size: 1.2rem;
      color: #ffffff;
      @media screen and (max-width: 767px) {
        font-size: 2.2rem;
      }
    }

    &:nth-of-type(1) {
      position: relative;
      padding-top: 1.7rem;
      transition: all 0.2s;
      &:hover {
        span:nth-of-type(2) {
          display: block;
        }
      }
      span:nth-of-type(1) {
        padding-bottom: 1rem;
        border-bottom: 1px solid white;
        @media screen and (max-width: 767px) {
          padding-bottom: 2rem;
        }
      }
      span:nth-of-type(2) {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-100%, 1.1rem);
        display: none;
        text-align: center;
        font-family: "Arial-Bold";
        font-size: 2.4rem;
        color: white;
        line-height: 6rem;
        width: 20rem;
        background-color: rgba(#2a2a2a, 0.8);

        @media screen and (max-width: 767px) {
          font-size: 3.4rem;
          width: 30rem;
        }
      }
    }

    &:nth-of-type(2) {
      padding-top: 1.4rem;
      @media screen and (max-width: 767px) {
        padding-top: 2.4rem;
      }
      span {
        padding-bottom: 1.1rem;
        border-bottom: 1px solid white;
        @media screen and (max-width: 767px) {
          padding-bottom: 2.1rem;
        }
      }
    }

    &:nth-of-type(3) {
      padding-top: 1.4rem;
      @media screen and (max-width: 767px) {
        padding-top: 2.4rem;
      }
      span {
        padding-bottom: 1.3rem;
        @media screen and (max-width: 767px) {
          padding-bottom: 2.3rem;
        }
      }
    }

    &:nth-of-type(4) {
      margin-top: 4rem;
      padding-top: 1.4rem;
      @media screen and (max-width: 767px) {
        margin-top: 5rem;
        padding-top: 2.4rem;
      }
      span {
        padding-bottom: 1.3rem;
        @media screen and (max-width: 767px) {
          padding-bottom: 2.3rem;
        }
      }
    }
  }
}
