.c-header__mobile {
  display: none;
  @media screen and (max-width: 767px) {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    grid-row: 1 / 2;
    grid-column: 1 /-1;
    height: 10rem;
    padding-left: 6rem;
    padding-right: 6rem;
    input {
      display: none;
    }

    input:checked + .c-header__toggle span:nth-of-type(2) {
      transform: translateX(-120%);
      visibility: hidden;
    }
    input:checked + .c-header__toggle span:nth-of-type(1) {
      display: block;
    }
    input:checked ~ .c-header__menu {
      display: flex;
    }
    .c-header__toggle {
      overflow: hidden;
      width: 8rem;
      height: 4rem;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      position: relative;
      &::after,
      &::before {
        display: block;
        content: "";
        width: 5rem;
        height: 6px;
        background-color: black;
        border-radius: 1rem;
      }
      span:nth-of-type(1) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 2rem;
        line-height: 1;
        text-transform: uppercase;
        color: red;
        font-weight: bold;
        display: none;
        transition: all 0.2s;
      }
      span:nth-of-type(2) {
        width: 5rem;
        height: 6px;
        background-color: black;
        border-radius: 1rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
        transition: all 0.2s;
      }
    }

    .c-header__menu {
      position: absolute;
      z-index: 1000;
      bottom: 0;
      left: 0;
      transform: translateY(100%);
      background-color: rgba(white, 0.8);
      width: 100%;
      height: 1000%;
      display: none;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-around;
      row-gap: 1rem;
      padding-left: 6rem;
      transition: all 0.2s;
      li {
        list-style: none;
        width: 100%;
        padding-bottom: 4rem;
        &:not(:last-child) {
          border-bottom: 2px dashed brown;
        }
        a {
          display: inline-block;
          text-decoration: none;
          font-family: "Yahei-regular";
          font-size: 3.6rem;
          line-height: 1;
          color: #444444;
        }
      }

      li:nth-of-type(1) {
        display: flex;
        justify-content: space-around;
        i {
          display: block;
          font-size: 3.6rem;
        }
      }

      li:last-child {
        a:nth-of-type(1) {
          display: inline-block;
          color: #1c9eb4;
          padding-right: 2rem;
          border-right: 1px solid black;
        }
        a:nth-of-type(2) {
          display: inline-block;
          padding-left: 2rem;
          border-left: 1px solid black;
        }
      }
    }
  }
}
