.p-content3 {
  grid-row: 5 /6;
  grid-column: 1 /-1;
  background-image: linear-gradient(to right, rgba(#0000, 0.6)),
    url(../img/body/content3/background.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  text-align: center;
  padding-bottom: 27.4rem;
  h1 {
    font-family: "Yahei-regular";
    font-weight: bold;
    font-size: 3.6rem;
    line-height: 6.6rem;
    color: #ffffff;
    padding-top: 25.5rem;
    margin-bottom: 0.5rem;
    letter-spacing: 0.05em;

    @media screen and (max-width: 767px) {
      font-size: 6.8rem;
      line-height: 1;
      letter-spacing: 0.1em;
      margin-bottom: 4rem;
    }
  }

  p {
    font-family: "Yahei-regular";
    color: #ffffff;
    font-size: 2.4rem;
    line-height: 5.4rem;
    letter-spacing: 0.05em;
    margin-bottom: 2.6rem;
    @media screen and (max-width: 767px) {
      font-size: 4.4rem;
      line-height: 1;
      letter-spacing: 0.1em;
      margin-bottom: 8rem;
    }
  }

  span {
    font-family: "Yahei-regular";
    color: #ffffff;
    font-size: 1.6rem;
    letter-spacing: 0.05em;

    @media screen and (max-width: 767px) {
      font-size: 3.6rem;
      line-height: 1;
      letter-spacing: 0.1em;
    }
    i {
      width: 8px;
      height: 14px;
      margin-left: 1rem;
    }
    &:not(:last-child) {
      margin-right: 13.2rem;
    }
  }
}
