/*------------------------------------------------------------
footer
------------------------------------------------------------*/
.l-footer {
  grid-row: 10 /11;
  grid-column: 1 /-1;
  background-color: #f0f0f0;
  padding-top: 5.1rem;
  @media screen and (max-width: 767px) {
    padding-top: 8rem;
  }
  &__top {
    width: 138rem;
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    margin: 0 auto;
    margin-bottom: 5.6rem;
  }

  &__box {
    display: flex;
    flex-direction: column;
    p {
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      color: #333333;
      line-height: 1;
      margin-bottom: 4.5rem;

      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 1;
      }
    }
    span {
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      color: #888888;
      line-height: 1;
      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 1;
      }

      &:not(:last-child) {
        margin-bottom: 2.8rem;
      }
    }
  }

  &__middle {
    width: 138rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-bottom: 2.2rem;
    margin: 0 auto;
    .l-footer__texts {
      display: inline-block;
      margin-left: 8.5rem;
      @media screen and (max-width: 767px) {
        margin-left: 5.5rem;
      }
    }
    span {
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      line-height: 1;
      color: #666666;
      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
      }
      &:not(:last-child) {
        margin-right: 2.6rem;
      }
    }

    span:nth-of-type(1) {
      color: #333333;
      font-size: 1.6rem;
      margin-right: 1.7rem;

      font-size: 2.6rem;
    }

    .l-footer__icons {
      display: flex;
      margin-right: 9rem;
      figure:not(:last-child) {
        margin-right: 2.2rem;
      }
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    border-top: 1px solid #cccccc;
    .l-footer__left {
      width: 37rem;
      display: flex;
      justify-content: space-between;
      margin-right: 69.1rem;
      @media screen and (max-width: 767px) {
        width: 60rem;
        margin-right: 395px;
      }
      span {
        font-family: "Yahei-regular";
        font-size: 1.2rem;
        line-height: 1;
        color: #888888;
        line-height: 5rem;

        @media screen and (max-width: 767px) {
          font-size: 2.2rem;
          line-height: 6rem;
        }
      }
    }
    .l-footer__right {
      span {
        font-family: "Yahei-regular";
        font-size: 1.2rem;
        line-height: 5rem;
        color: #888888;
        text-transform: uppercase;

        @media screen and (max-width: 767px) {
          font-size: 2.2rem;
          line-height: 6rem;
        }
      }
    }
  }
}
