.p-content7 {
  grid-row: 9 /10;
  grid-column: 1 /-1;

  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 138rem;
  margin: 0 auto;
  padding-top: 8rem;
  padding-bottom: 7.3rem;
  &__logo {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 18rem;
    p {
      display: inline-block;
      text-align: center;
      font-family: "Yahei-regular";
      font-size: 1.4rem;
      line-height: 2.6rem;
      color: #555555;
      @media screen and (max-width: 767px) {
        font-size: 2.4rem;
        line-height: 1.5;
        width: 20rem;
      }
    }
  }
}
